@import url(https://fonts.googleapis.com/css?family=Roboto:400,100,100italic,300,300italic,400italic,500,500italic,700,700italic,900,900italic&subset=latin,cyrillic);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.chat-container {
  position: fixed;
  right: 0; bottom: 0;
  box-shadow: 0px 0px 45px 1px rgba(0, 0, 0, 0.35);
  border-radius: 12px;
}

.bot-connection-container {
  height: 45px;
  color: #fff;
  background: #4aaef7;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.bot-connection-container > label {
  margin-left: 10px;
  font-size: 16px;
  font-weight: 540;
  line-height: 45px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.bot-connection-container > input {
  width: 225px;
  height: 21px;
  background-color: #4aaef7;
  color: #fff;
  font-weight: 540;
  font-size: 16px;
  text-indent: 5px;
  border-radius: 0;
  border: none;
  border-bottom: 0.25px solid white;
  outline: none;
}

.bot-connection-container > input::-webkit-input-placeholder {
  color: #fff;
  font-weight: 250;
}

.bot-connection-container > input::-ms-input-placeholder {
  color: #fff;
  font-weight: 250;
}

.bot-connection-container > input::placeholder {
  color: #fff;
  font-weight: 250;
}

.message-container {
  width: 400px;
  height: 500px;
  padding-bottom: 7.5px;
  text-align: left;
  overflow-y: scroll;
  display: flex;
  flex-flow: column nowrap;
}

.message-container .scroll-fix {
  flex: 1 1 auto;
}

.message-container::-webkit-scrollbar {
  width: 6px;
}

.message-container::-webkit-scrollbar-track {
  border-radius: 0;
}

.message-container::-webkit-scrollbar-thumb {
  margin: 2px;
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.2);
}

.send-message-form {
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  border-top: 1px solid #eee;
}

.custom-select-input-file, .send-message-button {
  width: 24px;
  height: 24px;
  background-size: 24px auto;
  display: block;
  background-repeat: no-repeat;
  cursor: pointer;
}

.custom-select-input-file {
  background-image: url(data:image/png;base64,);
}

.custom-select-input-file:hover {
  background-image: url(data:image/png;base64,);
}

#select-file {
  display: none;
}

.send-message-button {
  width: 20px;
  height: 20px;
  background-size: 20px auto;
  background-image: url(data:image/png;base64,);
}

.send-message-button:hover {
  background-image: url(data:image/png;base64,);
}

.message-input {
  height: 27px;
  outline: none;
  border: none;
  width: 70%;
  text-indent: 5px;
  color: #4b4b4b;
  font-size: 14px;
  border: none;
  border-bottom: 1px solid #eee;
}

.start-chat-btn {
  width: 40px;
  height: 40px;
  margin: 15px auto 10px;
  display: block;
  background: #42a5f5;
  border-radius: 50%;
  text-align: center;
  color: #f0f0f0;
  box-shadow: 0 0 4px rgba(0, 0, 0, .14), 0 4px 8px rgba(0, 0, 0, .28);
  cursor: pointer;
  outline: none;
}

.start-chat-btn:hover {
  box-shadow: 0 0 14px rgba(0, 0, 0, .14), 0 4px 18px rgba(0, 0, 0, .28);
}

.start-chat-btn i {
  font-size: 22px;
  line-height: 40px;
}

.start-chat-info {
  width: 50%;
  margin: 0 auto;
  font-size: 12px;
  color: #4b4b4b;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.blocker {
  position: absolute;
  width: 100%;
  height: 56px;
  background: white;
  opacity: .5;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
}

.blocker-hide {
  display: none;
}
.message-box {
    display: inline-block;
    max-width: 65%;
    padding: 8px 10px;
    margin: 8px 0 15px;
    border-radius: 3px;
    word-wrap: break-word;
    font: 14px/1em 'Roboto', 'PTSans', 'Arial', sans-serif;
    position: relative;
}

.outgoing {
    margin-right: 20px;
    background: #42a5f5;
    color: #eceff1;
    align-self: flex-end;
}

.incoming {
    height: auto;
    margin-left: 60px;
    background: rgba(0, 0, 0, 0.03);
    color: #666;
    align-self: flex-start;
    position: relative;
}

.incoming::before {
    width: 40px; height: 40px;
    border-radius: 50%;
    background-image: url(/static/media/bot-avatar.f375f8f4.png);
    background-repeat: no-repeat;
    background-size: 40px;
    position: absolute;
    left: -52px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    content: ' ';
    text-align: center;
}

.buttons-box {
    width: 65%;
    height: 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: none;
    padding: 8px 10px;
    margin: 15px 0 22px 60px;
}

.answer-button {
    min-width: 55px;
    height: 14px;
    padding: 5px 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    border: 1px solid rgb(3, 117, 208);
    color: rgb(3, 117, 208);
    cursor: pointer;
    font-size: 14px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.answer-button::first-line {
    text-transform: uppercase;
}

.answer-button:hover {
    background: rgb(3, 117, 208);
    color: #eceff1;
}

.image {
    padding: 0;
    background-color: transparent;
    background-color: initial;
    border-radius: 0;
}

.image img {
    max-width: 100%;
}

.image.incoming:hover, .image.outgoing:hover {
    background-color: transparent;
    background-color: initial;
    border-radius: 4px;
}

.file-download {
    font-size: .9em;
    margin-left: 20px;
    color: black;
    text-decoration: none;
    cursor: pointer;
}

.file-download::after {
    position: absolute;
    content: '';
    left: 5px;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background-size: 24px auto;
    height: 24px;
    width: 24px;
    background-image: url(data:image/png;base64,);
}

.file-size {
    text-align: end;
    font-size: .7em;
    color: #7c7c7c;
}

.voice-msg {
    height: 35px;
    max-width: 225px;
}

.message-time {
    color: rgba(0, 0, 0, .87);
    font-size: 10px;
    opacity: .3;
    position: absolute;
    bottom: -14px;
    right: 3px;
    cursor: default;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
